import React, {Component} from 'react';

class Product extends Component {

    render() {
        return (
            <div className="row margin-bottom-xs">
                <div className="col-7 col-sm-7 col-md-7 col-lg-9">
                    <input className="form-control" type="text" value="Sapun fancy"/>
                </div>

                <div className="col-2 col-sm-2 col-md-2 col-lg-1">
                    <div className="input-group mb-3">
                        <span className="product-multiply-position">x</span><input type="text" className="form-control" value="2"/>
                    </div>
                </div>
                <span className="product-equal-position">= 150 RON</span>
            </div>
        );
    }
}

export default Product;